* {
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
  font-family: "Dosis", sans-serif;
}

body {
  width: 100vw;
  overflow-x: hidden;
}

nav {
  display: flex;
  justify-content: space-between;
  width: 100%;
  min-height: 12vh;
  position: fixed;
  transition: 0.6s;
  z-index: 2;

  border-bottom-style: solid;
  border-width: 1px;
  border-color: #ddd;
}

nav.sticky {
  background: white;
  border-bottom: ridge;
  border-bottom-width: 1px;
  min-height: 9vh;
  z-index: 4;
}

nav.sticky a {
  color: black;
}

.logo {
  align-items: center;
  display: flex;
  text-align: center;
  justify-content: center;
  width: 40vw;
  z-index: 1;

  transition: 0.6s;
}

.logo img {
  height: 10vh;
}

.logo h1 {
  font-size: 6vh;
  display: flex;
  letter-spacing: 2px;
}

.logo a {
  color: black;
  text-decoration: none;
}

nav.sticky .logo img {
  height: 7vh;
}

nav.sticky .logo h1 {
  font-size: 5vh;
}

.nav-links {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 60%;
  z-index: 1;
}

.nav-links a {
  color: black;
  text-decoration: none;
  letter-spacing: 3px;
  font-size: 3vh;
}

.nav-links li {
  border-radius: 5px;
  font-size: calc(1vw + 2vh);
  list-style: none;
  text-align: center;
  width: 20%;
  line-height: 5vh;
}

.nav-links li:hover {
  /*background-color: rgb(218, 212, 131, 0.7);*/
  background-color: #f1dc9a;
}

.burger {
  display: none;
  cursor: pointer;
  align-self: center;
  margin-right: 20px;
  z-index: 3;
}

.burger div {
  width: 25px;
  height: 3px;
  background-color: black;
  margin: 5px;
  transition: all 0.3s ease;
}

@media screen and (max-width: 1024px) {
  .logo {
    font-size: 3vh;
  }
  .nav-links {
    width: 70%;
  }
}

@media screen and (max-width: 768px) {
  body {
    overflow-x: hidden;
  }
  .logo h1 {
    font-size: 5vh;
    display: flex;
    letter-spacing: 2px;
  }
  .nav-links {
    position: absolute;
    right: 0px;
    height: 100vh;
    background-color: white; /*#1b688c;*/
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    transform: translateX(100%);
    z-index: 2;
  }
  .nav-links li {
    /*opacity: 0;*/
    width: 50%;
  }
  .nav-links a {
    color: black;
  }
  .burger {
    display: block;
  }
  .logo {
    width: 80vw;
  }
}

.nav-active {
  transition: transform 0.5s ease-in;
  transform: translateX(0%);
}

@keyframes navLinkFade {
  from {
    opacity: 0;
    transform: translateX(50px);
  }
  to {
    opacity: 1;
    transform: translateX(0px);
  }
}

.toggle div {
  background-color: black;
}

.toggle .line1 {
  transform: rotate(-45deg) translate(-5px, 6px);
}
.toggle .line2 {
  opacity: 0;
}
.toggle .line3 {
  transform: rotate(45deg) translate(-5px, -6px);
}

/* Home
--------------------------------------------------------------
*/

.home {
  align-items: center;
  background-color: #faf2d9;
  flex-direction: column;
  display: flex;
  padding-bottom: 5vh;
  width: 100vw;
}

.home .row1 {
  display: flex;
  flex-direction: column;
  margin-top: 15vh;
  width: 400px;

  text-align: center;
  z-index: 1;
}

.home .signup {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.home .signup h2 {
  font-size: 7vh;
}

.home .signup .big-font {
  font-size: 3.5vh;
}

.home .signup p {
  font-size: 3vh;
  margin-top: 2vh;
}

.home .signup .signup-link {
  cursor: pointer;
  padding: 1.5vh 5vw;
  border-style: solid;
  border-width: 1px;
  text-align: center;
  border-radius: 10px;
  font-size: 3vh;
  margin: 3vh 0;

  background-color: #1340b1;
  border-color: white;
  color: white;
}

.home .signup .signup-link:hover {
  background-color: #1f57e7;
}

.home .row1 .upcoming {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;

  padding: 2vh 0;
  text-align: center;
}

@media screen and (min-width: 768px) {
  .home {
    padding-bottom: 0;
  }
  .home .row1 {
    align-items: normal;
    flex-direction: row;
    justify-content: space-between;
    text-align: left;
    width: 88vw;
  }
  .home .row1 .signup {
    width: 50%;
  }
  .home .signup .signup-link {
    width: 100%;
  }
  .home .signup a {
    /*display: flex;
    justify-content: center;*/
    text-decoration: none;
  }
  .home .row1 .upcoming {
    width: 44%;
  }
}

@media screen and (min-width: 1024px) {
  .home .row1 {
    width: 70vw;
  }
}

.home .upcoming .meal {
  height: 28vh;
  width: 49vh;
}

.home .upcoming h4 {
  font-size: 4vh;
  margin-bottom: 1vh;
}

.home .upcoming h5 {
  font-size: 3vh;
}

.home .stripe {
  align-items: center;
  display: flex;
  flex-direction: column;
  margin-top: 7vh;

  background-color: #f1dc9a;
  padding: 3vh 0 5vh;
  width: 100%;
}

.home .stripe h2 {
  font-size: 5vh;
}

.home .row2 {
  align-items: center;
  display: flex;
  flex-direction: column;
  width: 400px;

  text-align: center;
  z-index: 1;
}

.home .row2 .kekuni {
  margin-top: 3vh;
}

.home .row2 .uhcc {
  border-radius: 10px;
  margin-top: 5vh;
  height: 22vh;
  width: 40vh;
}

.home .row2 p {
  margin-top: 2vh;
  font-size: 2.5vh;
}

@media screen and (min-width: 768px) {
  .home .stripe {
    justify-content: center;
    display: flex;
    border-width: 1px;
    margin-top: 5vh;
    padding-bottom: 3vh;
  }

  .home .row2 {
    flex-direction: row;
    justify-content: space-between;
    width: 88vw;
  }
  .home .row2 p {
    margin-top: 0;
    width: 65%;
  }
  .home .row2 .kekuni {
    margin-top: 0;
    /*height: 20vh;*/
    height: 11vw;
  }
  .home .row2 .uhcc {
    margin-top: 0;
    height: 8.8vw;
    width: 16vw;
    /*height: 16.5vh;
    width: 30vh;*/
  }
}

@media screen and (min-width: 1024px) {
  .home .row2 {
    width: 70vw;
  }
  .home .row2 p {
    margin-top: 0;
    width: 55%;
  }
  .home .row2 .kekuni {
    height: 11vw;
  }
  .home .row2 .uhcc {
    height: 8.8vw;
    width: 16vw;
  }
}

/* Modules
--------------------------------------------------------------
*/

.modules {
  display: flex;
  justify-content: center;
  background-color: #faf2d9;
  /*background-color: white;*/
}

.invited {
  align-items: center;
  display: flex;
  flex-direction: column;
  margin-top: 2vh;
}

.invited h2 {
  font-size: 5vh;
  text-align: center;
}

.modules .descriptions {
  display: flex;
  flex-direction: column;
  margin-bottom: 10vh;
  justify-content: space-between;
  width: 400px;
}

.modules .descriptions .module {
  align-items: center;
  display: flex;
  flex-direction: column;
  text-align: center;
  width: 100%;
}

.modules .descriptions .module {
  width: 100%;
}

.modules .descriptions .module img {
  width: 100%;
}

.modules .descriptions .module p {
  margin-top: 1vw;
  font-size: 2.5vh;
}

.modules .invited h3 {
  margin-top: 3vh;
  font-size: 4vh;
  text-align: center;
  margin-bottom: 1vw;
}

@media screen and (min-width: 768px) {
  .modules {
    padding-top: 2vh;
  }
  .modules .descriptions {
    flex-direction: row;
    width: 80vw;
  }
  .modules .descriptions .module {
    width: 25vw;
  }
  .modules .descriptions .module img {
    width: 25vw;
  }

  .invited h2 {
    font-size: 7vh;
    text-align: center;
  }
  .modules .invited h3 {
    font-size: calc(2vh + 1.5vw);
  }
  .modules .descriptions .module p {
    font-size: calc(1vh + 0.8vw);
  }
}

/* Kukakuka
